import React, { Component } from 'react';
import Navigation from '../../components/navigation';

class bbprivete extends Component {
	componentDidMount() {
   	document.body.classList.add('single-work-active');	
  }
  componentWillUnmount() {
    document.body.classList.remove('single-work-active');
  }
  render() {
    const workid = 3;

    return (
      <div className="bbprivete">
        <div className="content">
          <h2 className="single-work__title">BB Privete</h2>
          <p>O Lorem Ipsum  um texto modelo da indstria tipogrfica e de impresso. O Lorem Ipsum tem vindo a ser o texto padro usado por estas indstrias desde o ano de 1500, quando uma misturou os caracteres de um texto para criar um espcime de livro. Este texto no s sobreviveu 5 sculos, mas tambm o salto para a tipografia electrnica, mantendo-se essencialmente inalterada. Foi popularizada nos anos 60 com a disponibilizao das folhas de Letraset, que continham passagens com Lorem Ipsum, e mais recentemente com os programas de publicao como o Aldus PageMaker que incluem verses do Lorem Ipsum.</p>
        </div>
        <Navigation workid={workid}></Navigation>
      </div>
      
    );
  }
}

export default bbprivete;



